export default {
    data() {
        return {
            list: [],
            count: 0,
            now_count: 0,
            page: 2,
            loading: false,
            more_list: false,
        }
    },
    methods: {
        getMore() {
            let that = this;
            that.$request({
                url: that.$api.step.invite_list,
                data: {
                    page: that.page
                },
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == 15) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.step.invite_list,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.count = response.data.count;
                    that.now_count = response.data.now_count;
                    that.more_list = false;
                    if (response.data.list.length == 15) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },
    onReachBottom: function () {
        if (!this.more_list) {
            this.getMore();
        }
    },
    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    }
}
